.navsWrapper {
  position: absolute;
  left: 0;
  top: 0;
  background: #222222;
  height: 640px;
  width: 100%;
}
.nav {
  position: absolute;
  right: 0;
  margin-top: 100px;
}
.navs {
  user-select: none;
  display: inline-flex;
  flex-direction: column;
  padding: 0;
  margin: 0;
  list-style: none;
  margin-right: 30px;
}
.link {
  font-size: 12px;
  text-decoration: none;
  text-transform: uppercase;
  color: #ffffff;
  background: none;
  border: none;
  padding: 0;
  transition: all 0.5s ease-out;
  font-family: "Poiret One";
  line-height: 18px;
}
.link:first-child {
  display: flex;
  align-items: center;
}
.i {
  font-size: 30px;
  color: #ffffff;
  transition: all .2s ease-in-out;
}
.iRotate {
  transform: rotate(180deg);
  color: #69c9ca;
}
.linkActive {
  color: #69c9ca;
}
.link:active, .link:focus {
  outline: none;
}
.listItem:not(:last-child) {
  margin-bottom: 34px;
}
.contacts {
  position: absolute;
  bottom: 0;
  color: #ffffff;
  text-align: center;
  width: 100%;
  font-size: 14px;
  font-weight: 300;
  user-select: none;
}
.contacts p {
  display: flex;
  justify-content: center;
  align-items: center;
}
.contacts p a {
  text-decoration: none;
  color: #fff;
  cursor: pointer;
}
.contacts p a:hover {
  text-decoration: none;
  color: #fff;
}
.contacts i {
  color: #69c9ca;
  margin-right: 10px;
}
@media(min-width: 1200px) {
  .navsWrapper {
    height: 100vh;
    background: url('../../../../img/background/services.jpg');
    background-image: -webkit-image-set(url('../../../../img/background/services.webp') 1x,  url('../../../../img/background/services.jpg') 1x);
    background-position: center;
    background-repeat: no-repeat;
    height: 100%;
    background-size: cover;
    background-position: 50% 30%;
  }
  .nav {
    margin-top: 160px;
  }
  .navs {
    margin-right: 100px;
  }
  .navs button {
    cursor: pointer;
  }
  .link {
    font-size: 24px;
    cursor: pointer;
    line-height: 36px;
  }
  .link:hover {
    color: #69c9ca;
  }
  .listItem:not(:last-child) {
    margin-bottom: 20px;
  }
}
