.wrapper {
  background: #222222;
  padding-bottom: 20px;
}
.button {
  display: block;
  padding: 3px 15px;
  border: none;
  background: #69C9CA;
  color: #F4F5F9;
  font-size: 14px;
  line-height: 36px;
  text-align: center;
  margin: 0 auto 100px auto;
  text-transform: uppercase;
  font-family: 'Open Sans';
  cursor: pointer;
}
@media(min-width:1200px) {
  .wrapper {
    background-image: url(../../img/background/pagesThirdRight.svg), url(../../img/background/pagesFifthLeft.svg), url(../../img/background/pagesFifthRight.svg), url(../../img/background/pagesLast.svg), url(../../img/background/seventhScreen.svg);
    background-repeat: no-repeat;
    background-size: 500 auto, 450 auto, 400 auto, 500 auto, 400px auto;
    background-position: 100% 1830px, 0 3400px, 100% 3340px, 100% 96%, 100% 73%;
  }
  .button {
    border: 2px solid #69C9CA;
    padding: 13px 0;
    color: #69C9CA;
    font-size: 18px;
    line-height: 30px;
    transition: all 0.3s ease;
    background: none;
    white-space: nowrap;
    width: 200px;
  }
  .button:hover {
    color: #F4F5F9;
    background: #69C9CA;
  }
}
